    <article id="rabla-eligibil">
        <h2>Ce VEHICULE sunt ELIGIBILE pentru PROGRAMUL RABLA ?</h2>
        <p>
            Motociclete sau scutere (L3e sau L4e) cu cilindree peste 50cc si viteza peste 45 km/h ce pot fi INMATRICULATE la DRPCIV si ale caror emisii NU depasesc 120g CO_2/km in sistem WMTC
            <br>
            <br>
            Achizitia ATV-urilor NU este permisa prin programul RABLA
        </p>
    </article>
    <article id="rabla-casat">
        <h2>Ce TREBUIE CASAT</h2>
        <p>
            In programul RABLA CLASSIC se pot casa DOAR autoturisme si se pot achizitiona motociclete, scutere sau autoturisme.
            <br><br>
            Pentru ncadrarea n programul Rabla, maina dus la casare trebuie s aib o vechime mai mare sau egal cu 6 ani de la prima nmatriculare in Romnia. Maina trebuie s conin cumulativ componentele eseniale (motor, transmisie, tren rulare, caroserie, asiu etc.) 
            <br><br>
            NU este necesar ca autoturismul sa fie proprietatea personala a solicitantului. Acesta poate participa in program si poate casa un autovehicul ce se afla in proprietatea altei persoane (fizice sau juridice) iar aceasta este de acord cu cedarea dreptului de casare.
            <br><br>
            Daca nu aveti autoturism pentru casare te putem ajuta cu procurarea unuia cu drept de casare. Solicitarile le puteti trimite la email <b> <a href="mailto:logistica@estbike.ro">logistica@estbike.ro</a></b>
        </p>
    </article>
    <article id="rabla-prime-casare">
        <h2>Care sunt PRIMELE de casare</h2>
        <p>
            Prima de casare pentru un vehicul ce are minim 6 ani vechime este de 6.000 Ron.
            <br><br>
            La aceast prim se pot aduga 2 ecobonusuri:
            <br><br>
            1. Pentru vehicul achiziionat cu emisie mai mic sau egal de 90g CO2/km  se adauga un ecobonus de 1500 Ron (aici se califica scuterele si motocicletele cu motor mai mic, doar dac se nmatriculeaz).
            <br><br>
            2. Pentru vehicul casat mai vechi de 15 ani (de la prima nmatriculare) i maxim Euro3  se adauga un ecobonus suplimentar de 1500 Ron
            <br><br>
            Prin urmare, clientul poate beneficia de 6000, 7500 sau 9000 de Ron, n funcie de criteriile de mai sus. Valorile includ TVA.
            <br><br>
            IMPORTANT: Suma tichetului Rabla i a ecobonusurilor nu poate depi 50% din valoarea produsului achiziionat (cu TVA inclus).
        </p>
    </article>
    <article id="rabla-acte-necesare">
        <h2>ACTELE NECESARE PT INSCRIEREA INSCRIEREA IN PROGRAM</h2>
        <p>
           <ul>
                <li>Buletinul (actul de identitate) al solicitantului</li>
                <br>
                <li>Buletinul proprietarului autovehiculului ce va fi casat (in cazul in care masina nu este proprietatea solicitantului)</li>
                <br>
                <li>Cartea de identitate a a vehiculului</li>
                <br>
                <li>Talonul vehiculului (certificatul de inmatriculare)</li>
                <br>
                <li>
                    Certificat fiscal de informare eliberat de Primariile sau Directia impozite si taxe locale de domiciliu, care sa ateste ca nu aveti datorii curente la bugetul local. Valabilitatea acestui document este de maxim 30 zile de la data emiterii. Fiscalul este necesar DOAR pt solicitantul particuparii in program.
                </li>
            </ul>
            <br><br>
            Certificat fiscal ANAF eliberat de Directia Regioanala a Finantelor Publice care sa ateste ca nu aveti datorii la ANAF. Valabilitatea acestui document este de maxim 90 zile de la data emiterii. Fiscalul este necesar DOAR pt solicitantul particuparii in program.
            
            <br><br>
            In cazul  unor situatii particulare solicitantul va trebui sa prezinte si urmatoarele documente:
            <br><br>
            <ul>
                <li>
                    certificatul de motenitor sau, n situaia succesiunii pe cale judectoreasc, sentina civil definitiv. Atunci cnd certificatul de motenitor ori  sentina civil atest existena mai multor motenitori, este necesar prezentarea unei mputerniciri notariale, n original, prin care persoana reprezentant este expres mandatat s acioneze i s ncheie acte juridice n cadrul programului, n numele, pe seama i n interesul tuturor comotenitorilor, n vederea: casrii autovehiculului uzat, radierii autovehiculului uzat din evidena circulaiei i achiziionrii autovehiculului nou;
                </li>
                <br>
                <li>
                    certificatul de cstorie sau orice act administrativ eliberat de ctre autoritatea competent material i teritorial, care atest schimbarea/modificarea numelui i/sau prenumelui, n copie certificat
                </li>
                <br>
                <li>
                    mputernicirea notarial, n original, prin care solicitantul mandateaz n mod expres o alt persoan s acioneze i s ncheie acte juridice n cadrul programului, n numele, pe seama i n interesul su, n vederea: casrii autovehiculului uzat, radierii autovehiculului uzat din evidena circulaiei, achiziionrii autovehiculului nou; n aceast situaie se va prezenta i actul care atest identitatea i domiciliul sau reedina n Romnia a persoanei mputernicite, eliberat de ctre autoritile romne, n termen de valabilitate, n copie certificata conforma cu originalul;
                </li>
            </ul>
        </p>
    </article>
