<script>
export default {
  data() {
    return {
      articles: [],
    };
  },
  mounted() {
    let articles = [];
    for (const child of document.getElementsByClassName("rabla")[0].children) {
      if (child.nodeName === "ARTICLE") {
        articles.push(child);
      }
    }
    this.articles = articles
    window.EventBus.dispatch('sectionnothome')
  },
  computed: {},
  methods: {
    goToSection(element) {
      let offset = 60
      let elementPos = element.getBoundingClientRect().top
      let position = elementPos + window.pageYOffset - offset

      window.scrollTo({
        top: position,
        behavior: "smooth"
      })
    },
  },
};
</script>
